* {
  box-sizing: border-box;
}

body {
  height: 100vh;
  margin: 0;
  background-color: #0B2434;
  padding: 20px;
  font-family: 'Karla', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}
main {
    background-color: #f5f5f5;
    height: 600px;
    max-width: 1024px;
    border-radius: 5px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.title {
    font-size: 3.2rem;
    margin: 0;
}

.instructions {
    font-family: "Inter", sans-serif;
    font-size: 1.3rem;
    font-weight: 400;
    margin-top: 0;
    text-align: center;
    max-width: 70%;
}

.stats {
    width: 60%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.best-roll-count {
    color: #037566;
}

.dice-container {
    display: grid;
    grid-template: auto auto / repeat(5, 1fr);
    grid-gap: 20px;
    gap: 20px;
    margin-bottom: 40px;
}

.roll-dice {
  height: 50px;
  width: 150px;
  border: none;
  border-radius: 6px;
  background-color: #5035FF;
  color: white;
  font-size: 1.2rem;
  font-family: 'Karla', sans-serif;
  cursor: pointer;
}

.roll-dice:focus {
  outline: none;
}

.roll-dice:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.face {
    width: 90px;
    height: 90px;
    padding: 6px;
    background: white;
    display: grid;
    grid-template: repeat(3, 1fr) / repeat(3, 1fr);
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    cursor: pointer;
    transition: linear 0.085s;
}

.dot {
    background: black;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    place-self: center;
}

.top {
    grid-row-start: 1;
}

.middle {
    grid-row-start: 2;
}

.bottom {
    grid-row-end: -1;
}

.left {
    grid-column-start: 1;
}

.center {
    grid-column-start: 2;
}

.right {
    grid-column-end: -1;
}

